import s from './TechSkills.module.scss';
import {
  DiJavascript1,
  DiReact,
  DiNodejs,
  DiMongodb,
  DiGit,
  DiFirebase,
  DiHtml5,
  DiCss3,
  DiSass,
} from 'react-icons/di';
import { SiTypescript, SiRedux, SiSocketDotIo } from 'react-icons/si';

const TechSkills = () => {
  return (
    <ul className={s.container}>
      <li className={s.techIcon}>
        <DiHtml5 />
        <h6>HTML5</h6>
      </li>
      <li className={s.techIcon}>
        <DiCss3 />
        <h6>CSS3</h6>
      </li>
      <li className={s.techIcon}>
        <DiSass />
        <h6>Sass</h6>
      </li>
      <li className={s.techIcon}>
        <DiJavascript1 />
        <h6>JavaScript</h6>
      </li>
      <li className={s.techIcon}>
        <SiTypescript />
        <h6>TypeScript</h6>
      </li>
      <li className={s.techIcon}>
        <DiNodejs />
        <h6>NodeJS</h6>
      </li>
      <li className={s.techIcon}>
        <DiReact />
        <h6>ReactJS</h6>
      </li>
      <li className={s.techIcon}>
        <DiMongodb />
        <h6>MongoDB</h6>
      </li>

      <li className={s.techIcon}>
        <SiRedux />
        <h6>Redux</h6>
      </li>

      <li className={s.techIcon}>
        <DiGit />
        <h6>Git</h6>
      </li>
      <li className={s.techIcon}>
        <DiFirebase />
        <h6>FireBase</h6>
      </li>
    </ul>
  );
};

export default TechSkills;
